<template>
    <div class="wrap">
        <template v-if="searchResultContent.type == 'briefCard'">
            <div
                class="authorItem"
                v-if="searchResultContent.data.follow.itemType == 'author'"
            >
                <div class="authorIcon">
                    <img :src="searchResultContent.data.icon" alt="" />
                </div>
                <div class="authorMessage">
                    <div class="authorName">
                        {{ searchResultContent.data.title }}
                    </div>
                    <div class="authorIntroduce">
                        {{ searchResultContent.data.description }}
                    </div>
                </div>
                <div class="attention">
                    <span>+</span>
                </div>
            </div>

            <div
                class="userItem"
                v-if="searchResultContent.data.follow.itemType == 'user'"
            >
                <div class="userIcon">
                    <img :src="searchResultContent.data.icon" alt="" />
                </div>
                <div class="userMessage">
                    <div class="userName">
                        {{ searchResultContent.data.title }}
                    </div>
                </div>
                <div class="attention">
                    <span>+</span>
                </div>
            </div>

            <div
                class="videoItem"
                v-if="searchResultContent.data.follow.itemType == 'video'"
            ></div>
        </template>

        <template v-else-if="searchResultContent.type == 'followCard'">
            <div class="videoItem" @click="$emit('toRouter')">
                <div class="image">
                    <img
                        :src="searchResultContent.data.content.data.cover.feed"
                        alt=""
                    />
                    <span class="time">{{ handleDuration(searchResultContent) }}</span>
                </div>
                <div class="explain">
                    <div class="name">
                        {{ searchResultContent.data.content.data.title }}
                    </div>
                    <div class="origin">
                        <span
                            >#{{
                                searchResultContent.data.content.data.category
                            }}</span
                        >
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    name: "SearchResultItem",
    props: ["searchResultContent"],
    methods: {
        handleDuration(item) {
            var m = parseInt(item.data.content.data.duration / 60);
            var s = item.data.content.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }

            return m + ":" + s;
        }
    }
};
</script>

<style lang="less">
div.attention {
    width: 6.5vh;
    margin-left: 10px;
    height: 8vh;
    line-height: 8vh;
    span {
        display: block;
        width: 6.5vh;
        height: 3.5vh;
        font-size: 12px;
        line-height: 3.5vh;
        text-align: center;
        color: #444444;
        font-weight: 600;
        border: 1px solid #808080;
        border-radius: 5px;
    }
}

.authorItem {
    width: 100%;
    height: 8vh;
    display: flex;
    padding: 10px 0;
    border-bottom: 1px solid rgb(236, 236, 236);

    .authorIcon {
        width: 8vh;
        height: 8vh;
        line-height: 8vh;
        margin-right: 10px;
        img {
            width: 7vh;
            height: 7vh;
            border-radius: 50%;
        }
    }
    .authorMessage {
        height: 8vh;
        flex: 1;
        .authorName {
            font-size: 14px;
            font-weight: 600;
            line-height: 16px;
            color: #444444;
            margin-bottom: 5px;
        }
        .authorIntroduce {
            font-size: 12px;
            font-weight: 500;
            color: #8c8c8c;
        }
    }
}
.userItem {
    width: 100%;
    height: 8vh;
    display: flex;
    padding: 10px 0;
    border-bottom: 1px solid rgb(236, 236, 236);
    .userIcon {
        width: 8vh;
        height: 8vh;
        line-height: 8vh;
        margin-right: 10px;
        img {
            width: 7vh;
            height: 7vh;
            border-radius: 50%;
        }
    }
    .userMessage {
        height: 8vh;
        flex: 1;
        .userName {
            font-size: 14px;
            font-weight: 600;
            line-height: 16px;
            color: #444444;
            margin-bottom: 5px;
        }
    }
}

.videoItem {
    margin-bottom: 4vw;
    padding-bottom: 2.5vw;
    border-bottom: 1px solid rgb(236, 236, 236);

    // font-size: 0;
    .image {
        width: 50vw;
        height: 29vw;
        display: inline-block;
        margin-right: 5vw;
        vertical-align: top;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
        }
        span {
            width: 35px;
            height: 20px;
            position: absolute;
            right: 5px;
            bottom: 5px;
            background: black;
            color: white;
            border-radius: 5px;
            font-size: 12px;
            text-align: center;
            line-height: 20px;
        }
    }
    .explain {
        width: 40vw;
        height: 30vw;
        display: inline-block;
        vertical-align: top;
        padding-top: 10px;
        box-sizing: border-box;
        position: relative;
        .name {
            font-size: 14px;
            color: #333333;
            line-height: 20px;
            font-weight: 600;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .origin {
            color: #bababa;
            font-size: 12px;
            position: absolute;
            bottom: 10px;
            left: 0;
        }
    }
}
</style>
