<template>
    <div class="dailypage">
        <ul>
            <li v-for="(item, index) in dailyContent" :key="index">
                <div class="daily-title" v-if="item.type == 'textCard'">
                    {{ item.data.text }}
                </div>
                <DailyPageItem
                    v-else-if="item.type == 'followCard'"
                    :duration="handleDuration(item)"
                    :item="item"
                    @toRouter="toPlay(item)"
                ></DailyPageItem>
            </li>
        </ul>
    </div>
</template>

<script>
import DailyPageItem from "@/components/DailyPageItem.vue";

export default {
    data() {
        return {
            dailyContent: null
        };
    },
    components: {
        DailyPageItem
    },
    methods: {
        getDailyContent() {
            let dailyContent = JSON.parse(
                window.localStorage.getItem("dailyContent")
            );

            if (dailyContent && Date.now() < dailyContent.time) {
                this.dailyContent = dailyContent.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v5/index/tab/feed?udid=ebc6079ccfd54a5ebbe81a6f619876bad91be323&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
                    )
                    .then(res => {
                        window.console.log(res.data.itemList);
                        this.dailyContent = res.data.itemList;

                        window.localStorage.setItem(
                            "dailyContent",
                            JSON.stringify({
                                data: this.dailyContent,
                                time: Date.now() + 30 * 60 * 1000
                            })
                        );
                    });
            }
        },
        handleDuration(item) {
            var m = parseInt(item.data.content.data.duration / 60);
            var s = item.data.content.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }

            return m + ":" + s;
        },
        toPlay(item) {
            window.console.log('xxx');
            
            this.$router.push({ path: "/play" });
            this.$store.commit("submitPlayMessage", item);
        }
    },
    created() {
        this.getDailyContent();
    }
};
</script>

<style lang="less">
.dailypage {
    padding: 0 2.5vw;
    ul {
        li {
            .daily-title {
                font-size: 20px;
                color: #444444;
                line-height: 50px;
                font-weight: 600;
            }
        }
    }
}
</style>
