<template>
    <div class="recommend">
        <!--  -->
        <div class="choiceness">
            <HomeTitle></HomeTitle>
            <ul>
                <CrunchiesItem
                    v-for="(item, index) in dailyChoiceness"
                    :key="index"
                    :item="item"
                    :duration="handleDuration(item)"
                    :isHome="false"
                    @toRouter="toPlay(item)"
                ></CrunchiesItem>
            </ul>
        </div>

        <!--  -->
        <div class="message">
            <HomeTitle @toRouter="toAllInformation"></HomeTitle>
            <InformationItem
                :information="dailyInformation"
                :isShowTime="false"
            ></InformationItem>
        </div>
    </div>
</template>

<script>
import HomeTitle from "../components/HomeTitle.vue";
import CrunchiesItem from "../components/CrunchiesItem.vue";
import InformationItem from "../components/InformationItem.vue";

export default {
    data() {
        return {
            dailyChoiceness: [],
            dailyInformation: {
                textCard: null,
                informationCard: null
            },
            information: []
        };
    },
    components: {
        HomeTitle,
        CrunchiesItem,
        InformationItem
    },
    methods: {
        getChoiceness() {
            this.axios
                .get(
                    "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v5/index/tab/allRec?page=0&isOldUser=true"
                )
                .then(res => {
                    this.dailyChoiceness = res.data.itemList[0].data.itemList;
                });
        },
        getInformation() {
            let information = JSON.parse(
                window.localStorage.getItem("information")
            );

            if (information && Date.now() < information.time) {
                this.information = information.data;
                this.dailyInformation.textCard = information.data[0].textCard;
                this.dailyInformation.informationCard =
                    information.data[0].informationCard;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v7/information/list?udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
                    )
                    .then(res => {
                        var obj = {
                            textCard: null,
                            informationCard: null
                        };
                        window.console.log(res);
                        this.dailyInformation.textCard = res.data.itemList[0];
                        this.dailyInformation.informationCard =
                            res.data.itemList[1];
                        for (let i = 0; i < res.data.itemList.length; i++) {
                            if (i % 2 == 0) {
                                obj.textCard = res.data.itemList[i];
                            } else {
                                obj.informationCard = res.data.itemList[i];
                                this.information[parseInt(i / 2)] = obj;

                                obj = {
                                    textCard: null,
                                    informationCard: null
                                };
                            }
                        }

                        window.localStorage.setItem(
                            "information",
                            JSON.stringify({
                                data: this.information,
                                time: Date.now() + 60 * 60 * 1000
                            })
                        );
                    });
            }
        },
        handleDuration(item) {
            var m = parseInt(item.data.content.data.duration / 60);
            var s = item.data.content.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }
            return m + ":" + s;
        },
        toAllInformation() {
            this.$router.push({ path: "/allinformation" });
            this.$store.commit("submitInformation", this.information);
        },
        toPlay(item) {
            window.console.log("xxx");
            this.$store.commit("submitPlayMessage", item);
            this.$router.push({ path: "/play" });
        }
    },
    created() {
        this.getChoiceness();
        this.getInformation();
    }
};
</script>

<style lang="less">
.recommend {
    padding: 0 2.5vw;
    .message {
        border-bottom: 1px solid rgb(236, 236, 236);
    }
}
</style>
