<template>
    <div class="specailtopic">
        <div class="specailtopic-title">
            <span class="fa fa-angle-left" @click="$router.go(-1)"></span>
            
        </div>
        <ul class="specailtopic-items">
            <li v-for="(item, index) in specailtopicItems" :key="index">
                <img :src="item.data.image" alt="" />
            </li>
        </ul>
        <End></End>
    </div>
</template>

<script>
import End from "../components/End.vue";

export default {
    data() {
        return {
            specailtopicItems: []
        };
    },
    components:{
        End
    },
    methods: {
        getSpecailtopic() {
            let specailtopicItems = JSON.parse(
                window.localStorage.getItem("specailtopicItems")
            );
            if (specailtopicItems && Date.now() < specailtopicItems.time) {
                this.specailtopicItems = specailtopicItems.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v3/specialTopics?udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
                    )
                    .then(res => {
                        window.console.log(res.data.itemList);
                        this.specailtopicItems = res.data.itemList;
                        window.localStorage.setItem(
                            "specailtopicItems",
                            JSON.stringify({
                                data: this.specailtopicItems,
                                time: Date.now() + 24 * 30 * 60 * 1000
                            })
                        );
                    });
            }
        }
    },
    created() {
        this.getSpecailtopic();
    }
};
</script>

<style lang="less">
.specailtopic {
    padding-top: 8vh;
    .specailtopic-title {
        width: 100%;
        height: 8vh;
        line-height: 8vh;
        background: #fafafa;
        color: #444444;
        text-align: center;
        position: relative;
        font-size: 20px;
        font-weight: 600;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 5;
        span {
            width: 8vh;
            height: 8vh;
            position: absolute;
            top: 0;
            left: 0;
            font-size: 30px;
            line-height: 8vh;
            text-align: center;
        }
    }
    ul {
        width: 100%;
        box-sizing: border-box;
        padding: 0 2.5vw;
        li {
            width: 100%;
            height: 55vw;
            padding: 2.5vw 0;
            border-bottom: 1px solid rgb(236, 236, 236);

            img {
                width: 100%;
                height: 100%;
                border-radius: 5px;
            }
        }
    }
}
</style>
