<template>
    <ul class="searchResult">
        <li v-for="(item, index) in searchResult" :key="index">
            <div class="searchResult-title" v-if="item.type == 'textCard'">
                {{ item.data.text }}
            </div>
            <SearchResultItem
                :searchResultContent="item"
                v-else-if="item.type != 'textCard'"
                @toRouter="toPlay(item)"
            ></SearchResultItem>
        </li>
    </ul>
</template>

<script>
import SearchResultItem from "@/components/SearchResultItem.vue";

export default {
    name: "SearchResult",
    props: ["searchResult"],
    components: {
        SearchResultItem
    },
    methods: {
        toPlay(item) {
            window.console.log("xxx");
            this.$store.commit("submitPlayMessage", item);
            this.$router.push({ path: "/play" });
        }
    }
};
</script>

<style lang="less">
.searchResult {
    li {
        .searchResult-title {
            font-size: 20px;
            color: #444444;
            line-height: 50px;
            font-weight: 600;
        }
    }
}
</style>
