<template>
    <div class="wrap">
        <div
            class="play"
            :style="{
                backgroundImage:
                    'url(' + playMessage.data.content.data.cover.blurred + ')'
            }"
            v-if="playMessage.data.content"
        >
            <video
                :src="playMessage.data.content.data.playUrl"
                controls
            ></video>
            <span
                class="fa fa-angle-left goback"
                @click="$router.go(-1)"
            ></span>
            <div class="play-content">
                <h3 class="name">{{ playMessage.data.content.data.title }}</h3>
                <div class="origin">
                    <span>#{{ playMessage.data.content.data.category }}</span> /
                    <span></span>
                </div>
                <p class="description">
                    {{ playMessage.data.content.data.description }}
                </p>
                <div class="play-data">
                    <span class="collect">
                        <i class="fa fa-heart-o"></i>
                        {{
                            playMessage.data.content.data.consumption
                                .collectionCount
                        }}
                    </span>
                    <span class="download">
                        <i class="fa fa-sign-out"></i>
                        {{
                            playMessage.data.content.data.consumption.shareCount
                        }}
                    </span>
                    <span>
                        <i class="fa fa-sign-in"></i>
                        
                    </span>
                    <span>
                        <i class="fa fa-star-o"></i>
                        
                    </span>
                </div>
            </div>
            <div class="play-origin">
                <div class="portrait">
                    <img
                        :src="playMessage.data.content.data.author.icon"
                        alt=""
                    />
                </div>
                <div class="text">
                    <div class="name">
                        {{ playMessage.data.content.data.author.name }}
                    </div>
                    <div class="introduction">
                        {{ playMessage.data.content.data.author.description }}
                    </div>
                </div>
                <span class="attention">+</span>
            </div>

            <div class="play-recommend">
                <div class="play-recommend-title"></div>
                <ul class="recommend-list">
                    <PlayRecommendListItem
                        :item="item"
                        :duration="handleDuration(item)"
                        v-for="(item, index) in recommendList"
                        :key="index"
                    ></PlayRecommendListItem>
                </ul>
            </div>

            <div class="more"></div>
        </div>

        <div
            class="play"
            :style="{
                backgroundImage: 'url(' + playMessage.data.cover.blurred + ')'
            }"
            v-else
        >
            <video :src="playMessage.data.playUrl" controls></video>
            <span
                class="fa fa-angle-left goback"
                @click="$router.go(-1)"
            ></span>
            <div class="play-content">
                <h3 class="name">{{ playMessage.data.title }}</h3>
                <div class="origin">
                    <span>#{{ playMessage.data.category }}</span> /
                    <span></span>
                </div>
                <p class="description">
                    {{ playMessage.data.description }}
                </p>
                <div class="play-data">
                    <span class="collect">
                        <i class="fa fa-heart-o"></i>
                        {{ playMessage.data.consumption.collectionCount }}
                    </span>
                    <span class="download">
                        <i class="fa fa-sign-out"></i>
                        {{ playMessage.data.consumption.shareCount }}
                    </span>
                    <span>
                        <i class="fa fa-sign-in"></i>
                        
                    </span>
                    <span>
                        <i class="fa fa-star-o"></i>
                        
                    </span>
                </div>
            </div>
            <div class="play-origin">
                <div class="portrait">
                    <img :src="playMessage.data.author.icon" alt="" />
                </div>
                <div class="text">
                    <div class="name">
                        {{ playMessage.data.author.name }}
                    </div>
                    <div class="introduction">
                        {{ playMessage.data.author.description }}
                    </div>
                </div>
                <span class="attention">+</span>
            </div>

            <div class="play-recommend">
                <div class="play-recommend-title"></div>
                <ul class="recommend-list">
                    <PlayRecommendListItem
                        :item="item"
                        :duration="handleDuration(item)"
                        v-for="(item, index) in recommendList"
                        :key="index"
                    ></PlayRecommendListItem>
                </ul>
            </div>

            <div class="more"></div>
        </div>
    </div>
</template>

<script>
import PlayRecommendListItem from "../components/PlayRecommendListItem.vue";

export default {
    data() {
        return {
            playMessage: null,
            recommendList: []
        };
    },
    components: {
        PlayRecommendListItem
    },
    methods: {
        getPlayMessage() {
            this.playMessage = this.$store.state.playMessage;
        },
        getRecommendList() {
            if (this.playMessage.data.content) {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v4/video/related?id=" +
                            this.playMessage.data.content.data.id +
                            "&vc=580&vn=6.1&deviceModel=MI%20MAX%202"
                    )
                    .then(res => {
                        window.console.log(res.data);
                        this.recommendList = res.data.itemList.filter(
                            item => item.type == "videoSmallCard"
                        );
                    });
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v4/video/related?id=" +
                            this.playMessage.data.id +
                            "&vc=580&vn=6.1&deviceModel=MI%20MAX%202"
                    )
                    .then(res => {
                        window.console.log(res.data);
                        this.recommendList = res.data.itemList.filter(
                            item => item.type == "videoSmallCard"
                        );
                    });
            }
        },
        handleDuration(item) {
            var m = parseInt(item.data.duration / 60);
            var s = item.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }

            return m + ":" + s;
        },
        toPlay(item) {
            window.console.log("xxx");
            this.$store.commit("submitPlayMessage", item);
            this.$router.push({ path: "/play" });
        }
    },
    created() {
        this.getPlayMessage();
        this.getRecommendList();
    }
};
</script>

<style lang="less">
.play {
    width: 100vw;
    // height: 100vh;
    background-position: center;
    background-size: cover;
    position: relative;
    video {
        width: 100%;
    }
    span.goback {
        font-size: 30px;
        color: white;
        position: absolute;
        top: 2.5vw;
        left: 2.5vw;
        z-index: 10;
    }
    .play-content {
        background: rgba(0, 0, 0, 0.2);
        padding: 3vw 2.5vw;
        color: white;
        .name {
            font-size: 16px;
            font-weight: 600;
        }
        .origin {
            font-size: 13px;
            font-weight: 300;
            line-height: 40px;
        }
        .description {
            font-size: 15px;
            font-weight: 300;
            line-height: 20px;
            margin-bottom: 15px;
        }
        .play-data {
            display: flex;
            span {
                flex: 1;

                &:nth-child(2) {
                    i {
                        transform: rotate(-90deg);
                    }
                }

                &:nth-child(3) {
                    i {
                        transform: rotate(90deg);
                    }
                }
            }
        }
    }
    .play-origin {
        width: 100%;
        height: 12vh;
        display: flex;
        padding: 0 2.5vw;
        box-sizing: border-box;
        background: rgba(0, 0, 0, 0.1);
        align-items: center;
        text-align: center;
        // border-bottom: 1px solid rgb(236, 236, 236);

        .portrait {
            // width: 10vh;
            margin-right: 2vw;
            height: 12vh;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: left;

            img {
                width: 7vh;
                height: 7vh;
                border-radius: 50%;
            }
        }
        .text {
            height: 10vh;
            padding-top: 1vh;
            flex: 1;
            text-align: left;
            color: white;
            .name {
                font-size: 14px;
                font-weight: 600;
                line-height: 6vh;
            }
            .introduction {
                font-size: 14px;
                overflow: hidden;
                text-overflow: ellipsis;
                font-weight: 300;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }
        }
        span.attention {
            display: inline-block;
            width: 7vh;
            height: 4vh;
            font-size: 13px;
            line-height: 4vh;
            text-align: center;
            color: white;
            font-weight: 600;
            border: 1px solid white;
            border-radius: 5px;
        }
    }
    .play-recommend {
        padding: 0 2.5vw;
        padding-top: 5vw;
        .play-recommend-title {
            font-size: 16px;
            color: white;
            font-weight: 600;
            margin-bottom: 4vw;
        }
    }
    .more {
        text-align: center;
        font-size: 14px;
        font-weight: 300;
        color: white;
        line-height: 50px;
    }
}
</style>
