import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    classifys: [],
    information: null,
    playMessage: null
  },
  mutations: {
    submitClassify(state, arr) {
      state.classifys = arr
    },
    submitInformation(state, obj) {
      state.information = obj
    },
    submitPlayMessage(state, obj) {
      state.playMessage = obj
    }
  },
  actions: {
  },
  modules: {
  }
})
