<template>
    <div class="found">
        <!--  -->
        <div class="swiper">
            <van-swipe :autoplay="5000" :show-indicators="false">
                <van-swipe-item
                    v-for="swiper in swipers"
                    :key="swiper.data.id"
                    style="width"
                >
                    <img :src="swiper.data.image" alt="" />
                </van-swipe-item>
            </van-swipe>
        </div>

        <!--  -->
        <div class="hot-classify">
            <HomeTitle @toRouter="$router.push({ path: '/classify' })"
                ></HomeTitle
            >
            <ul class="cliassify-items">
                <li
                    v-for="(item, index) in classifysPart"
                    :key="item.id"
                    :class="{ hint: (index - 1) % 3 == 0 }"
                >
                    #{{ item.name }}
                    <div
                        class="bg"
                        :style="{
                            backgroundImage: 'url(' + item.bgPicture + ')'
                        }"
                    ></div>
                </li>
            </ul>
        </div>

        <!--  -->
        <div class="specialtopic">
            <HomeTitle @toRouter="$router.push({ path: '/specailtopic' })"
                ></HomeTitle
            >
            <ul class="specialtopic-items">
                <li
                    v-for="(specialtopic, index) in specialtopics"
                    :key="specialtopic.data.id"
                    :class="{ hint: index % 2 == 0 }"
                >
                    <p class="text">
                        {{ specialtopic.data.title }}
                    </p>
                    <img :src="specialtopic.data.image" alt="" />
                </li>
            </ul>
        </div>

        <!--  -->
        <div class="week-crunchies">
            <HomeTitle @toRouter="$router.push({ path: '/leaderboard' })"
                ></HomeTitle
            >
            <ul>
                <CrunchiesItem
                    v-for="item in crunchies"
                    :key="item.data.id"
                    :item="item"
                    :duration="handleDuration(item)"
                    :isHome="true"
                    @toRouter="toPlay(item)"
                ></CrunchiesItem>
            </ul>
        </div>

        <!--  -->
        <div class="recommend-topic">
            <HomeTitle></HomeTitle>
            <ul class="topic-items">
                <TopicItem
                    v-for="(topic, index) in topics"
                    :key="index"
                    :item="topic"
                ></TopicItem>
            </ul>
        </div>

        <!--  -->
        <End></End>
    </div>
</template>

<script>
import HomeTitle from "../components/HomeTitle.vue";
import CrunchiesItem from "../components/CrunchiesItem.vue";
import TopicItem from "../components/TopicItem.vue";
import End from "../components/End.vue";

export default {
    data() {
        return {
            swipers: [],
            specialtopics: [],
            columnImgUrl: null,
            crunchies: [],
            topics: [],
            classifys: [],
            classifysPart: []
        };
    },
    components: {
        HomeTitle,
        CrunchiesItem,
        TopicItem,
        End
    },
    methods: {
        getContent() {
            let homedata = JSON.parse(window.localStorage.getItem("homedata"));

            if (homedata && Date.now() < homedata.time) {
                homedata.data.forEach(element => {
                    if (element.type == "horizontalScrollCard") {
                        // window.console.log(index, element.data.itemList);

                        this.swipers = element.data.itemList;
                    } else if (element.type == "columnCardList") {
                        this.specialtopics = element.data.itemList;
                    } else if (element.type == "banner") {
                        this.columnImgUrl = element.data.image;
                    } else if (element.type == "videoSmallCard") {
                        this.crunchies.push(element);
                    } else if (element.type == "briefCard") {
                        this.topics.push(element);
                    }
                });
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v7/index/tab/discovery?udid=ebc6079ccfd54a5ebbe81a6f619876bad91be323&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
                    )
                    .then(res => {
                        window.console.log(res.data);

                        res.data.itemList.forEach((element, index) => {
                            if (element.type == "horizontalScrollCard") {
                                window.console.log(
                                    index,
                                    res.data.itemList[index].data.itemList
                                );

                                this.swipers =
                                    res.data.itemList[index].data.itemList;
                            } else if (element.type == "columnCardList") {
                                this.specialtopics =
                                    res.data.itemList[index].data.itemList;
                            } else if (element.type == "banner") {
                                this.columnImgUrl =
                                    res.data.itemList[index].data.image;
                            } else if (element.type == "videoSmallCard") {
                                this.crunchies.push(res.data.itemList[index]);
                            } else if (element.type == "briefCard") {
                                this.topics.push(res.data.itemList[index]);
                            }
                            window.localStorage.setItem(
                                "homedata",
                                JSON.stringify({
                                    data: res.data.itemList,
                                    time: Date.now() + 30 * 60 * 1000
                                })
                            );
                        });
                    });
            }
        },
        getClassify() {
            var classifys = JSON.parse(
                window.localStorage.getItem("classifys")
            );
            if (classifys) {
                this.classifys = classifys;
                this.classifysPart = classifys.slice(0, 6);
                this.$store.commit("submitClassify", this.classifys);
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v4/categories/"
                    )
                    .then(res => {
                        window.console.log(res.data);
                        this.classifys = res.data;
                        this.classifysPart = res.data.slice(0, 6);
                        window.localStorage.setItem(
                            "classifys",
                            JSON.stringify(res.data)
                        );
                    });
                this.$store.commit("submitClassify", this.classifys);
            }
        },
        handleDuration(item) {
            var m = parseInt(item.data.duration / 60);
            var s = item.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }

            return m + ":" + s;
        },
        toPlay(item) {
            window.console.log("xxx");
            this.$store.commit("submitPlayMessage", item);
            this.$router.push({ path: "/play" });
        }
    },
    created() {
        this.getContent();
        this.getClassify();
    }
};
</script>

<style lang="less">
.found {
    padding: 0 2.5vw;
    border-bottom: 1px solid rgb(236, 236, 236);

    .swiper {
        padding: 2.5vw 0;
        box-sizing: border-box;
        width: 95vw;
        height: 30vh;
        border-bottom: 1px solid rgb(236, 236, 236);

        .van-swipe {
            width: 100%;
            height: 100%;
            .van-swipe-item {
                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 5px;
                }
            }
        }
    }
    .hot-classify {
        border-bottom: 1px solid rgb(236, 236, 236);
        ul {
            overflow: hidden;
            padding-bottom: 2.5vw;
            li {
                width: 31vw;
                height: 31vw;
                line-height: 31vw;
                margin-bottom: 1vw;
                border-radius: 5px;

                float: left;

                text-align: center;
                background-color: rgba(0, 0, 0, 0.3);

                color: white;

                position: relative;
                &.hint {
                    margin: 0 1vw;
                }
                .bg {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: -1;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center;
                    border-radius: 5px;
                }
            }
        }
    }

    .specialtopic {
        border-bottom: 1px solid rgb(236, 236, 236);

        .specialtopic-items {
            padding: 2.5vw 0;
            padding-top: 0;

            li {
                display: inline-block;
                width: 47vw;
                height: 28vw;
                border-radius: 5px;
                margin-bottom: 1vw;
                text-align: center;
                // line-height: 28px;
                overflow: hidden;
                position: relative;
                // padding: 0 10vw;
                box-sizing: border-box;
                // padding-top: 10vw;
                background: rgba(52, 0, 0, 0.3);

                p.text {
                    width: 75%;
                    // height: 100%;
                    // margin: 0 auto;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-family: "";
                    font-size: 14px;
                    line-height: 20px;
                    color: white;
                    word-wrap: break-word;
                }

                &.hint {
                    margin-right: 1vw;
                }
                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    // height: 100%;
                    border-radius: 5px;
                    z-index: -1;
                }
            }
        }
    }
    .column {
        border-bottom: 1px solid rgb(236, 236, 236);
        .column-item {
            width: 100%;
            height: 50vw;
            border-radius: 5px;
            padding-bottom: 2.5vw;
            img {
                width: 100%;
                height: 100%;
                border-radius: 5px;
            }
        }
    }
    .week-crunchies {
        border-bottom: 1px solid rgb(236, 236, 236);
    }
}
</style>
