<template>
    <div class="home">
        <div class="nav">
            <span class="history" @click="showDayCover"
                ><i class="fa fa-calendar"></i
            ></span>
            <ul>
                <li>
                    <router-link to="/recommend"></router-link>
                </li>
                <li>
                    <router-link to="/found"></router-link>
                </li>
                <li>
                    <router-link to="/dailypaper"></router-link>
                </li>
            </ul>
            <span class="search-icon" @click="showSearch"
                ><i class="fa fa-search"></i
            ></span>
        </div>
        <keep-alive>
            <router-view />
        </keep-alive>
        <van-popup v-model="isShowSearch" position="top" :overlay="true">
            <Search @exit="exitSearch" :keywords="keywords"></Search>
        </van-popup>
        <van-popup v-model="isDayCover" position="top" :overlay="true">
            <DayCover
                @exit="exitDayCover"
                :dayCoverContent="dayCoverContent"
            ></DayCover>
        </van-popup>
    </div>
</template>

<script>
import Search from "@/components/Search.vue";
import DayCover from "@/components/DayCover.vue";

export default {
    name: "home",
    data() {
        return {
            currentMode: "found",
            isShowSearch: false,
            keywords: [],
            dayCoverContent: null,
            isDayCover: false
        };
    },
    components: {
        Search,
        DayCover
    },
    methods: {
        showSearch() {
            this.isShowSearch = true;
            this.getKeywords();
        },
        exitSearch() {
            this.isShowSearch = false;
        },
        getKeywords() {
            let keywords = JSON.parse(window.localStorage.getItem("keywords"));

            if (keywords && Date.now() < keywords.time) {
                this.keywords = keywords.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v3/queries/hot"
                    )
                    .then(res => {
                        window.console.log(res.data);
                        this.keywords = res.data;
                        window.localStorage.setItem(
                            "keywords",
                            JSON.stringify({
                                data: res.data,
                                time: Date.now() + 24 * 60 * 60 * 1000
                            })
                        );
                    });
            }
        },
        getdayCoverContent() {
            let dayCoverContent = JSON.parse(
                window.localStorage.getItem("dayCoverContent")
            );

            if (dayCoverContent && Date.now() < dayCoverContent.time) {
                this.dayCoverContent = dayCoverContent.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/DayCover/getDayCover"
                    )
                    .then(res => {
                        this.dayCoverContent = res.data;
                        window.console.log(res.data, this.dayCoverContent);
                        window.localStorage.setItem(
                            "dayCoverContent",
                            JSON.stringify({
                                data: res.data,
                                time: Date.now() + 24 * 60 * 60 * 1000
                            })
                        );
                    });
            }
        },
        showDayCover() {
            window.console.log('xxx');
            
            this.isDayCover = true;
            this.getdayCoverContent();
        },
        exitDayCover() {
            this.isDayCover = false;
        }
    },
    created() {
        if (window.localStorage.getItem("dayCoverContent")) {
            this.dayCoverContent = JSON.parse(
                window.localStorage.getItem("dayCoverContent")
            ).data;
        }
    }
};
</script>
