import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Recommend from '../views/Recommend.vue'
import Classify from '../views/Classify.vue'
import Found from '../views/Found.vue'
import DailyPaper from '../views/DailyPaper.vue'
import Leaderboard from '../views/Leaderboard.vue'
import LeaderboardWeek from '../views/LeaderboardWeek.vue'
import LeaderboardMonth from '../views/LeaderboardMonth.vue'
import LeaderboardTotal from '../views/LeaderboardTotal.vue'
import SpecailTopic from '../views/SpecailTopic.vue'
import AllInformation from '../views/AllInformation.vue'
import Play from '../views/Play.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    redirect: '/found',
    children: [
      {
        path: '/found',
        component: Found
      },
      {
        path: '/recommend',
        component: Recommend
      },
      {
        path: '/dailypaper',
        component: DailyPaper
      }
    ]
  },
  {
    path: '/classify',
    name: 'classify',
    component: Classify
  },
  {
    path: '/leaderboard',
    name: 'leaderboard',
    component: Leaderboard,
    redirect: '/leaderboardWeek',
    children: [
      {
        path: '/leaderboardWeek',
        component: LeaderboardWeek
      },
      {
        path: '/leaderboardMonth',
        component: LeaderboardMonth
      },
      {
        path: '/leaderboardTotal',
        component: LeaderboardTotal
      }
    ]
  },
  {
    path: '/specailtopic',
    name: 'specailtopic',
    component: SpecailTopic
  },
  {
    path: '/allinformation',
    name: 'allinformation',
    component: AllInformation
  },{
    path: '/play',
    name: 'play',
    component: Play
  },
]

const router = new VueRouter({
  routes
})

export default router
