<template>
    <div class="classify">
        <div class="classify-title">
            <span
                class="fa fa-angle-left"
                @click="$router.push({ path: '/found' })"
            ></span>
            
        </div>
        <ul class="clssifys-other">
            <li
                :style="{
                    backgroundImage: 'url(' + classifyOthers[0] + ')'
                }"
                @click="$router.push({ path: '/leaderboard' })"
            ></li>
            <li
                :style="{
                    backgroundImage: 'url(' + classifyOthers[1] + ')'
                }"
                @click="$router.push({ path: '/specailtopic' })"
            ></li>
            <li
                :style="{
                    backgroundImage: 'url(' + classifyOthers[2] + ')'
                }"
            ></li>
        </ul>
        <ul class="clssifys-itmes">
            <li
                v-for="(classify, index) in classifys"
                :key="index"
                :class="{ hint: index % 2 == 0 }"
            >
                #{{ classify.name }}
                <div
                    class="bg"
                    :style="{
                        backgroundImage: 'url(' + classify.bgPicture + ')'
                    }"
                ></div>
            </li>
        </ul>
        <End></End>
    </div>
</template>

<script>
import End from "@/components/End.vue";

export default {
    data() {
        return {
            classifys: [],
            classifyOthers: []
        };
    },
    components: {
        End
    },
    methods: {
        getClassifys() {
            this.classifys = this.$store.state.classifys;
        },
        getClassifysOther() {
            let classifyOthers = JSON.parse(
                window.localStorage.getItem("classifyOthers")
            );
            if (classifyOthers && Date.now() < classifyOthers.time) {
                this.classifyOthers = classifyOthers.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v4/categories/all?udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
                    )
                    .then(res => {
                        window.console.log(res.data.itemList);
                        for (var i = 0; i < 3; i++) {
                            this.classifyOthers.push(
                                res.data.itemList[i].data.image
                            );
                        }
                        window.localStorage.setItem(
                            "classifyOthers",
                            JSON.stringify({
                                data: this.classifyOthers,
                                time: Date.now() + 24 * 30 * 60 * 1000
                            })
                        );
                    });
            }
        }
    },
    created() {
        this.getClassifysOther();
        this.getClassifys();
    }
};
</script>

<style lang="less">
.classify {
    padding-top: 8vh;
    .classify-title {
        width: 100%;
        height: 8vh;
        line-height: 8vh;
        background: white;
        color: #444444;
        text-align: center;
        position: relative;
        font-size: 20px;
        font-weight: 600;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 5;
        span {
            width: 8vh;
            height: 8vh;
            position: absolute;
            top: 0;
            left: 0;
            font-size: 30px;
            line-height: 8vh;
            text-align: center;
        }
    }
    ul.clssifys-other {
        overflow: hidden;
        li {
            width: 49.5vw;
            height: 49.5vw;
            margin-bottom: 0.5vw;
            float: left;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            &:first-child {
                margin-right: 1vw;
            }
            &:last-child {
                width: 100%;
            }
        }
    }

    ul.clssifys-itmes {
        overflow: hidden;
        li {
            width: 49.5vw;
            height: 49.5vw;
            line-height: 49.5vw;
            margin-bottom: 0.5vw;
            float: left;
            text-align: center;
            background-color: rgba(0, 0, 0, 0.2);
            color: white;
            position: relative;
            &.hint {
                margin-right: 1vw;
            }
            .bg {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
}
</style>
