<template>
    <div class="search">
        <!-- <form action="/"> -->
        <div class="form">
            <input
                type="text"
                name=""
                id=""
                v-model="searchContent"
                placeholder="     "
                @keyup.enter="search(searchContent)"
            />
            <span class="clear" v-if="searchContent" @click="clearContent"
                ></span
            >
            <span class="fa fa-search icon" v-if="!searchContent"></span>
            <span class="exit" @click="exit"></span>
        </div>
        <!-- </form> -->
        <div class="searching" v-if="isSearching">
            <div class="history" v-if="!(historyWords.length == 0)">
                <div class="history-title">
                    
                    <span class="delete" @click="deleteHistory"></span>
                </div>
                <ul>
                    <li
                        v-for="(historyWord, index) in historyWords"
                        :key="index"
                        @click="search(historyWord)"
                    >
                        {{ historyWord }}
                    </li>
                </ul>
            </div>
            <div class="keyword">
                <div class="keyword-title"></div>
                <ul>
                    <li
                        v-for="(keyword, index) in keywords"
                        :key="index"
                        @click="search(keyword)"
                    >
                        {{ keyword }}
                    </li>
                </ul>
            </div>
        </div>

        <div class="searchResult" v-else>
            <div class="loading" v-if="loading">
                <img src="../assets/loading2.gif" alt="" />
            </div>
            <SearchResult :searchResult="searchResult" v-else></SearchResult>
        </div>
    </div>
</template>

<script>
import SearchResult from "@/components/SearchResult.vue";

export default {
    name: "Search",
    props: ["keywords"],
    data() {
        return {
            searchContent: null,
            historyWords: [],
            isSearching: true,
            searchResult: [],
            loading: false
        };
    },
    components: {
        SearchResult
    },
    methods: {
        exit() {
            this.isSearching = true;
            this.searchContent = null;
            this.$emit("exit");
        },
        clearContent() {
            this.isSearching = true;
            this.searchContent = "";
            window.console.log(this.isSearching);
            // this.$set(this.isSearching,true)
        },
        search(word) {
            this.searchContent = word;
            if (this.historyWords.join().indexOf(word) == -1) {
                this.historyWords.unshift(this.searchContent);
            }
            this.isSearching = false;
            this.getSearchResult();
        },
        deleteHistory() {
            this.historyWords = [];
        },
        getSearchResult() {
            this.loading = true;
            this.axios
                .get(
                    "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v3/search?query=" +
                        this.searchContent
                )
                .then(res => {
                    this.searchResult = res.data.itemList;
                    window.console.log(this.searchResult);
                    this.loading = false;
                });
        }
    },
    watch: {
        historyWords() {
            window.localStorage.setItem(
                "historyKeywords",
                JSON.stringify(this.historyWords)
            );
        },
        searchContent() {
            if (this.searchContent == "") {
                this.isSearching = false;
            }
        }
    },
    created() {
        if (JSON.parse(window.localStorage.getItem("historyKeywords"))) {
            this.historyWords = JSON.parse(
                window.localStorage.getItem("historyKeywords")
            );
        }
    }
};
</script>

<style lang="less">
.search {
    width: 100%;
    height: 100%;
    padding: 2.5vw;
    box-sizing: border-box;

    .loading {
        width: 30px;
        height: 30px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        img {
            width: 100%;
        }
    }

    .form {
        width: 100%;
        height: 5vh;
        display: flex;
        position: relative;
        // margin-bottom: 15px;
        input {
            flex: 1;
            height: 100%;
            background: #efefef;
            line-height: 5vh;
            border-radius: 5px;
            font-size: 13px;
            padding-left: 20px;
            box-sizing: border-box;
            border: none;
            outline: none;
            ::-webkit-input-placeholder {
                color: #bdbdbd;
            }
        }
        span.clear {
            position: absolute;
            top: 50%;
            width: 14px;
            height: 14px;
            right: 18vw;
            transform: translateY(-50%);
            border-radius: 50%;
            color: #bdbdbd;
            border: 1px solid #bdbdbd;
            text-align: center;
            line-height: 12px;
        }
        span.icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 22px;
            color: #bdbdbd;
            font-size: 13px;
        }
        span.exit {
            display: inline-block;
            width: 13vw;
            height: 100%;
            color: #717171;
            font-size: 16px;
            line-height: 5vh;
            text-align: center;
            margin-left: 2.5vw;
        }
    }

    .searching {
        .history {
            .history-title {
                font-size: 20px;
                font-weight: 600;
                color: #444444;
                line-height: 50px;
                // margin-bottom: 15px;

                span {
                    float: right;
                    font-size: 14px;
                    color: #789ac9;
                    font-weight: 500;
                }
            }
            ul {
                margin-bottom: 2.5vw;
                li {
                    font-size: 14px;
                    font-weight: 600;
                    color: #789ac9;
                    padding: 12px 0;
                    border-bottom: 1px solid rgb(236, 236, 236);
                }
            }
        }

        .keyword {
            .keyword-title {
                font-size: 20px;
                font-weight: 600;
                color: #444444;
                line-height: 50px;
                // margin-bottom: 15px;
            }
            ul {
                li {
                    font-size: 14px;
                    font-weight: 600;
                    color: #789ac9;
                    padding: 12px 0;
                    border-bottom: 1px solid rgb(236, 236, 236);
                }
            }
        }
    }
}
</style>
