<template>
    <div class="leaderboardMonth">
        <ul class="leaderboardMonth-itmes">
            <LeaderboardItem
                v-for="(item, index) in leaderboardMonthItems"
                :key="index"
                :item="item"
                :duration="handleDuration(item)"
                @toRouter="toPlay(item)"
            ></LeaderboardItem>
        </ul>
        <End></End>
    </div>
</template>

<script>
import LeaderboardItem from "../components/LeaderboardItem.vue";
import End from "../components/End.vue";

export default {
    data() {
        return {
            leaderboardMonthItems: []
        };
    },
    components: {
        LeaderboardItem,
        End
    },
    methods: {
        getLeaderboardWeek() {
            let leaderboardMonthItems = JSON.parse(
                window.localStorage.getItem("leaderboardMonthItems")
            );
            if (
                leaderboardMonthItems &&
                Date.now() < leaderboardMonthItems.time
            ) {
                this.leaderboardMonthItems = leaderboardMonthItems.data;
            } else {
                this.axios
                    .get(
                        "https://api.kele8.cn/agent/https://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=monthly&start=0&num=35"
                    )
                    .then(res => {
                        window.console.log(res);
                        this.leaderboardMonthItems = res.data.itemList;

                        window.localStorage.setItem(
                            "leaderboardMonthItems",
                            JSON.stringify({
                                data: this.leaderboardMonthItems,
                                time: Date.now() + 30 * 60 * 1000
                            })
                        );
                    });
            }
        },
        handleDuration(item) {
            var m = parseInt(item.data.duration / 60);
            var s = item.data.duration % 60;
            if (m < 10) {
                m = "0" + m;
            }
            if (s < 10) {
                s = "0" + s;
            }
            return m + ":" + s;
        },
        toPlay(item) {
            window.console.log("xxx");
            this.$store.commit("submitPlayMessage", item);
            this.$router.push({ path: "/play" });
        }
    },
    created() {
        this.getLeaderboardWeek();
    }
};
</script>

<style lang="less">
.leaderboardMonth {
    width: 100%;
    padding: 1vw 2.5vw;
    box-sizing: border-box;
    ul.leaderboardMonth-itmes {
        width: 100%;
    }
}
</style>
